.customModal {
    .mantine-Modal-title {
      font-size: 24px;
      color: red;
      text-shadow: 1px 1px #000000;
    }
  }
  .commentsection {
    padding: 0 20px 20px 20px;
    .comments {
      display: flex;
     justify-content: space-between;
      border-bottom: 1px solid #888686;
      & > div:first-child {
        display: flex;
        gap: 20px;
        & > div:first-child {
          display: flex;
        }
      }
      img {
        max-width: 40px;
        min-width: 40px;
        height: 40px;
      }
      .username {
        display: flex;
        flex-direction: column;
        span:nth-of-type(1) {
          font-size: 20px;
          font-weight: 600;
        }
        span:nth-of-type(2) {
          font-size: 14px;
        }
      }
    }
   
    // .commentsender > div {
    //   padding: 0px 15px 0px 15px;
    // }
  }
  .commentsender {
    background: white;
    display: flex;
    justify-content: space-between;
    height: 3rem;
    align-items: center;
    gap: 1rem;
    padding: 0.8rem;
    border-radius: 1rem;
    align-self: end;
    input {
      height: 50%;
      min-width: 50%;
      background-color: rgb(236, 236, 236);
      border-radius: 0.5rem;
      border: none;
      outline: none;
      flex: 1;
      font-size: 14px;
      padding: 0px 15px 0px 15px;
    }
  }
  